import React from 'react';
import {HashRouter as Router, Switch, Route} from 'react-router-dom';
import Navbar from './Navbar';
import Home from './Home';
import Lions from './Lions';
import Tigers from './Tigers';
import Bears from './Bears';


function App() {
  return (
    <div className="container m-3">
      <Router>
        <Navbar />
        <div className="container jumbotron m-3">
        <Switch>          
          <Route exact path="/lions" component={Lions} />
          <Route exact path="/tigers" component={Tigers} />
          <Route exact path="/bears" component={Bears} />
          <Route path="/" component={Home} />
          <Route path="*" render={ () => <h1>404 - Not Found</h1> } />
        </Switch>
        </div>
      </Router>
    </div>
  );
}

export default App;
