import React from 'react'
import {NavLink, Link} from 'react-router-dom';
import './NavBar.css';
const Navbar = () => {
    return (
        <div className="container">
            <nav className="navbar navbar-expand-lg navbar-dark bg-dark">      
                <Link className="navbar-brand" to="/">React-Deploy-Test</Link>
                    <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
                        <span className="navbar-toggler-icon"></span>
                    </button>
                <div className="collapse navbar-collapse" id="navbarNav">
                    <ul className="navbar-nav">
                    <li className="nav-item">
                        <NavLink className="nav-link" activeClassName="selected" exact to="/">Home <span className="sr-only">(current)</span></NavLink>
                    </li>
                    <li className="nav-item">
                        <NavLink className="nav-link" activeClassName="selected" exact to="/lions">Lions</NavLink>
                    </li>
                    <li className="nav-item">
                        <NavLink className="nav-link" activeClassName="selected" exact to="/tigers">Tigers</NavLink>
                    </li>      
                    <li className="nav-item">
                        <NavLink className="nav-link" activeClassName="selected" exact to="/bears">Bears</NavLink>
                    </li>      
                    </ul>
                </div>
            </nav>           
    </div>
    )
}

export default Navbar;